import { Component, OnInit } from "@angular/core";
import { DataService } from "../../data.service";
import {
  NgxGalleryOptions,
  NgxGalleryImage,
  NgxGalleryAnimation
} from "ngx-gallery";


@Component({
  selector: "app-category-detail",
  templateUrl: "./category-detail.component.html",
  styleUrls: ["./category-detail.component.scss"]
})
export class CategoryDetailComponent implements OnInit {
  galleryOptions: NgxGalleryOptions[];
  galleryImages: NgxGalleryImage[];
  productdetail:any[];
  constructor(private data:DataService) { }

  ngOnInit() {
    this.getDetails();
    this.galleryOptions = [
      {
        width: "100%",
        height: "400px",
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide
      },
      // max-width 800
      {
        breakpoint: 800,
        width: "100%",
        height: "600px",
        imagePercent: 80,
        thumbnailsPercent: 20,
        thumbnailsMargin: 20,
        thumbnailMargin: 20
      },
      // max-width 400
      {
        breakpoint: 400,
        preview: false
      }
    ];
    this.galleryImages = [
      {
        small: "assets/img/lr.jpg",
        medium: "assets/img/lr.jpg",
        big: "assets/img/lr.jpg"
      },
      {
        small: "assets/img/lr2.jpg",
        medium: "assets/img/lr2.jpg",
        big: "assets/img/lr2.jpg"
      },
      {
        small: "assets/img/lr3.jpg",
        medium: "assets/img/lr3.jpg",
        big: "assets/img/lr3.jpg"
      },
      {
        small: "assets/img/lr2.jpg",
        medium: "assets/img/lr2.jpg",
        big: "assets/img/lr2.jpg"
      },
      {
        small: "assets/img/lr.jpg",
        medium: "assets/img/lr.jpg",
        big: "assets/img/lr.jpg"
      }
    ];
    
  }
  getDetails(){
    this.data.getParticular().subscribe(res => {this.productdetail = res});
  }
  
}
