import { Component, OnInit } from '@angular/core';
import { HostListener } from "@angular/core";
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  closeResult: string;
  loginform: FormGroup;
  SignupForm: FormGroup;
  submitted = false;
  submit = false;
  isScrolled = false;
  currPos: Number = 0;
  startPos: Number = 0;
  changePos: Number = 100;
  constructor(private modalService: NgbModal, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.SignupForm = this.formBuilder.group({
      Signup_Name: ['', Validators.required],
      Signup_Email: ['', Validators.required],
      Signup_Password: ['', [Validators.required]],
      Signup_Confirmpassword: ['', [Validators.required]]
    });
  }
  modalLogin(content) {
    this.modalService.open(content, { centered: true });
  }

  modalsigin(logincontent) {
    this.modalService.open(logincontent, { centered: true });
  }
  onSubmited() {
    this.submit = true;
    if (this.loginform.invalid) {
      return;
    }
  }
  @HostListener("window:scroll", [])
  onWindowScroll() {
    const currPos = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    if (currPos >= this.changePos) {
      this.isScrolled = true;
    } else {
      this.isScrolled = false;
    }
  }
}
