import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';
@Injectable({
  providedIn: "root"
})
export class DataService {
  constructor(private http: HttpClient,private _scrollToService: ScrollToService) {
  
  }
  public triggerScrollTo() {
    
    const config: ScrollToConfigOptions = {
      container: 'custom-container',
      target: 'destination',
      duration: 500,
      easing: 'easeInOutQuad',
      offset: 100
    };
 
    this._scrollToService.scrollTo(config);
  }
  getData(){
    return this.http.get<any[]>('https://bbrevamp.herokuapp.com/landing');
  }
  getDetail(){
    return this.http.get<any[]>('https://bbrevamp.herokuapp.com/product');
  }
  getParticular(){
    return this.http.get<any[]>('https://bbrevamp.herokuapp.com/product/5bb7433c0209b4000410622d');
  }
  postData(data){
    return this.http.post<any[]>('https://bbrevamp.herokuapp.com/postProduct', JSON.stringify(data),{headers: { 'Content-Type': 'application/json'}});
  }
}
