import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { HomeComponent } from './home/home.component';
import { CategoryListComponent } from './category-list/category-list.component';
import { CategoryDetailComponent } from './category-detail/category-detail.component';
import { AddDetailsComponent } from './add-details/add-details.component';
import { LoginpageComponent } from '../auth/loginpage/loginpage.component';
import { CategoryMapComponent } from './category-map/category-map.component';
import { ProfileComponent } from './profile/profile.component';
import { AddImageComponent } from './add-image/add-image.component';
import { AddCategoryComponent } from './add-category/add-category.component';
import { AddSpecificComponent } from './add-specific/add-specific.component';

const routes: Routes = [{
  path: '',
  component: LayoutComponent,
  children: [
    {
      path: '',
      redirectTo: 'home',
      pathMatch: 'full'
    },
    {
      path: 'home',
      component: HomeComponent
    },
    {
      path: 'category-list',
      component: CategoryListComponent
    },
    {
      path: 'category-detail',
      component: CategoryDetailComponent
    },
    {
      path: 'add-details',
      component: AddDetailsComponent
    },
    {
      path: 'loginpage',
      component: LoginpageComponent
    },
    {
      path: 'category-map',
      component: CategoryMapComponent
    },
    {
      path: 'my-profile',
      component: ProfileComponent
    },
    {
      path: 'add-image',
      component: AddImageComponent
    },
    {
      path: 'add-category',
      component: AddCategoryComponent
    },
    {
      path: 'add-specific',
      component: AddSpecificComponent
    }
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LayoutRoutingModule { }
