import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../../data.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
@Component({
  selector: 'app-add-details',
  templateUrl: './add-details.component.html',
  styleUrls: ['./add-details.component.scss']
})
export class AddDetailsComponent implements OnInit {
  addForm: FormGroup;
  returnUrl: string;
  productlist: any[];
  list:any[];
  submitted = false;
  constructor(private data: DataService, private builder: FormBuilder,private router: Router) {
    this.addForm = this.builder.group({
      productCost: ['', Validators.required],
      productTitle: ['', Validators.required],
      productDescription: ['', Validators.required]
      // productName: ['', Validators.required]
      // productPostedBy: ['', Validators.required],
      // catid: ['', Validators.required],
      // productAddress: ['', Validators.required],
      // productContactNo: ['', Validators.required],
      // productThunder: ['', Validators.required]
    })
  }
  ngOnInit() {
    this.getData();
    this. gestList();
  }
  
  getData() {
    this.data.getDetail().subscribe(res => { this.productlist = res });
  }
  gestList() {
    this.data.getData().subscribe(res => {this.list = res});
  }
  onSubmit() {
    this.submitted = true;
    if (this.addForm.invalid) {
      return;
    }
    this.data.postData(this.addForm.value).subscribe(res => {
      this.router.navigateByUrl(this.returnUrl);
    });
  }
}
